.projects {
    padding-top: 2rem 2rem;
    &__header {
        font-size: 40px;
        padding-bottom: 30px;
        text-align: center;
    }
}

.project {
    display: grid;
    grid-template-columns: 1fr 3fr;
    gap: 1rem;
    font-family: sans-serif;
    // span {
    //     border-top: 1px solid #000;
    //     padding: 1rem;
    //     font-size: 30px;
    //     text-align: center;
    // }
    &__infos {
        margin-top: 3rem;
        &--name {
            position: relative;
            left: 5%;
            font-size: 32px;
            font-family: sans-serif;
            // display: flex;
            // align-items: center;
            font-weight: 300;
        }
        &--tags {
            left: 5%;
            position: relative;
            bottom: 1rem;
            display: flex;
            flex-direction: column;
            gap: 1rem;
        }
    }
    &__img {
        position: relative;
        width: 100%;
        height: 700px;
        img {
            width: 100%;
            height: 600px;
            object-fit: cover;
            right: 10vw;
            top: 3rem;
        }
    }
    &__links {
        // padding-top: 3rem;
        button {
            &:before {
                background: #000;
            }
            &:after {
                display: none;
            }
        }
    }
}

@media (max-width: 1350px) {
    .projects {
        padding: 3rem;
    }
    .projects__header {
        font-size: 35px;
        padding-bottom: 20px;
    }
    .project__infos {
        margin-top: 2rem;
    }
    .project__infos--name {
        font-size: 30px;
        text-align: center;
    }
    .project__img img {
        width: 100%;
        right: 5rem;
        object-fit: cover;
        object-position: 1% 0;
    }
    .project .coolCircleEyeButton {
        transform: translate(-19rem, -2rem)
    }
}

@media (max-width: 700px) {
    .projects {
        padding: 1rem;
    }
    .projects__header {
        font-size: 25px;
        padding-bottom: 0px;
    }
    .project {
        display: grid;
        grid-template-columns: 1fr;
        place-items: center;     
    }
    .project__infos--link {
        display: grid;
        place-items: center;    
    }
    .project__infos--name {
        font-size: 20px;
        left: 0%;
    }
    .project__infos--tags {
        flex-direction: row;
        top: 0;
        left: 0;
    }
    .project__img img {
        width: 100%;
        right: 3rem;
        object-fit: cover;
        object-position: 1% 0;
    }
    .project .coolCircleEyeButton {
        transform: translate(-23rem, -1rem)
    }
}

@media screen {
    .project .coolCircleEyeButton {
        transform: translate(0);
    }
    .project__tags {
        gap: 6px;
        font-size: 14;
    }
    .project__header {
        font-size: 25px;
    }
}
